import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ProjectLoginComponent } from '../components/auth/login/login';
import { ProjectRegisterComponent } from '../components/auth/register/register';
import { ProjectForgotPasswordComponent } from '../components/auth/forgot-password/forgot-password';
import { ProjectGuestGuard } from '../guards/auth.guard';

const routes: Routes = [
  {
    path: 'login',
    component: ProjectLoginComponent,
    canActivate: [ProjectGuestGuard]
  },
  {
    path: 'register',
    component: ProjectRegisterComponent,
    canActivate: [ProjectGuestGuard]
  },
  {
    path: 'forgot-password',
    component: ProjectForgotPasswordComponent,
    canActivate: [ProjectGuestGuard]
  },
  {
    path: '',
    redirectTo: 'login',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProjectAuthRoutingModule { }